@mixin flex($x: center, $y: center){
    display: flex; justify-content: $x; align-items: $y;
    @content;
}
@mixin font($x: inherit, $y: inherit, $z: inherit){
    font-size: $x;
    font-weight: $y;
    color: $z;
}

*{padding: 0; margin: 0; box-sizing: border-box;}

main{
    outline: 1px solid black;
    background-color: greenyellow;

    margin: 100px auto;
    padding: 50px;
    @include flex(space-between);
    width: 1350px;
    
    figure{
        @include flex(){flex-direction: column};
        background-color: white;
        padding: 20px;

        p{
            width: 300px; height: 200px;
            img{width: 100%; height: 100%;}
        }
        
        div.txtWrapper{
            margin: 20px 0;
            @include flex(){flex-direction: column};
            @include font(1.5rem, 600, black);
            
            figcaption{
                @include font(2rem, bold, darkgreen);

            }
            span{
                margin-top: 5px;
                &.detailBtn{
                    margin-top: 10px;
                    border: 3px solid darkgreen;
                    padding: 5px 10px;

                    transition: .2s;
                    a{
                        color: darkgreen;
                        text-decoration: none;
                    }

                    &:hover{
                        background-color: darkgreen;
                        a{
                            color: white;
                        }
    
                    }
                    &:active{
                        border: 3px solid rgb(0, 50, 0);
                        background-color: rgb(0, 50, 0);
                        a{
                            color: gold;
                        }
                    }
                }//&.detailBtn

            }//span

        }//div.txtWrapper
    }//figure


    figure.detail{
        margin: auto;

        p{
            width: 600px; height: 400px;
            img{}
        }
        
        div.txtWrapper{
            
            figcaption{

            }
            span{
                &.detailBtn{
                    a{
                    }

                    &:hover{
                        a{
                        }
    
                    }
                    &:active{
                        a{
                        }
                    }
                }//&.detailBtn

            }//span

        }//div.txtWrapper
    }//figure
}//main